<template>
    <div class="p-d-flex p-jc-center">
        <p-card class="p-col-4">
            <template #content>
                <user-form :type="formType" @onSave="save" />
            </template>
        </p-card>
    </div>
</template>

<script>
import UserForm from "@/components/users/form.vue";
import { FORM_TYPE } from "@/config";
import { useStore } from "vuex";
import { useToast } from "primevue/usetoast";
import { useRouter } from "vue-router";

export default {
    components: {
        UserForm
    },
    setup() {
        const store = useStore();
        const toast = useToast();
        const router = useRouter();

        const save = (isValid, form) => {
            if (!isValid) {
                return;
            }

            store
                .dispatch("users/createUser", {
                    ...form,
                    privilage: form.role
                })
                .then(() => {
                    toast.add({
                        severity: "success",
                        summary: "Success",
                        detail: "Create Successfull",
                        life: 3000
                    });
                    router.push({ path: "/users" });
                });
        };

        return {
            save,
            formType: FORM_TYPE.CREATE
        };
    }
};
</script>
